* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
  text-transform: capitalize;
}

nav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 80px;
  width: 100%;
  /* background-color: white; */
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  /* position: fixed; */
  /* z-index: 100 !important; */
 
}

nav img {
  width: 60px;
  margin: 0 40px;
}
nav ul {
  width: 40%;
  display: flex;
  justify-content: space-between;
  margin: 0px 60px 0 0;
}
nav ul li {
  list-style: none;
  padding: 30px 10px;

  transition: 0.8s;
}
nav ul > li a {
  text-decoration: none;
  font-size: 13px;
  color: rgba(0, 0, 0, 0.774);
  text-transform: uppercase;
  font-weight: 600;
}
.home-icons {
  display: flex;
  width: 20%;
  justify-content: space-around;
  text-align: center;
  align-items: center;
  /* border: 1px solid red; */
}
.profile,
.home-icons p,
.bag,
.wish {
  cursor: pointer;
}
.home-icons p {
  font-size: 12px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.913);
}

sup {
  font-size: 10px;
  color: #ff4293;
  font-weight: 800;
}
.search {
  width: 20%;
}
#search {
  height: 40px;
  width: 95%;
  padding: 0 10px;
  background-color: rgba(213, 227, 232, 0.596);
  border: none;
  outline: none;
}
nav ul li:nth-child(1):hover {
  border-bottom: 2px solid #f66b25;
}
nav ul li:nth-child(2):hover {
  border-bottom: 2px solid #ff4293;
}

nav ul li:nth-child(3):hover {
  border-bottom: 2px solid #fb6d25;
}
nav ul li:nth-child(4):hover {
  border-bottom: 2px solid #ff950b;
}
nav ul li:nth-child(5):hover {
  border-bottom: 2px solid #0097a7;
}
/* drop down  */

.dropdown {
  visibility: hidden;
  position: absolute;
  top: 13.5%;
  display: flex;
  line-height: 30px;
  left: 10%;
  width: 70%;
  z-index: 2;
  justify-content: space-between;
  background-color: white;
  box-shadow: rgba(70, 70, 78, 0.2) 0px 7px 29px 0px;

  height: auto;
}
.dropdown > div {
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
}
.dropdown > div:nth-child(2n) {
  background-color: aliceblue;
  padding: 20px 40px;
}
.dropdown div a {
  font-size: 12px;
  text-transform: capitalize;
  color: rgba(0, 0, 0, 0.701);
}
.dropdown span .tops {
  color: #fb6d25;
  font-size: 0.9rem;
}
.dropdown span .tops2 {
  color: #ff4293;
}
.dropdown span .tops3 {
  color: #ff950b;
}
.dropdown span .tops4 {
  color: #0097a7;
}
nav ul li:hover .dropdown {
  visibility: visible;
  transition: all 0.5s;
  transform: scale(1.1, 1);
}
#menu {
  display: none;
  font-size: 1.8rem;
  margin: 0 20px;
}

/*=============================== DROP PROFILE STYLING================================== */

.dropProf {
  visibility: hidden;
  position: absolute;
  top: 14%;
  display: flex;
  line-height: 30px;
  left: 70%;
  width: 25%;
  z-index: 2;
  background-color: white;
  box-shadow: rgba(70, 70, 78, 0.2) 0px 7px 29px 0px;
  height: auto;
  flex-direction: column;
  padding: 15px 20px;
  text-transform: capitalize;
  border: 1px solid rgba(0, 0, 0, 0.42);
}

.dropProf>div:nth-child(1) h3{
  float: left;
  color: rgba(0, 0, 0, 0.788);
}
.dropProf>div:nth-child(1) p{
  float: left;
  font-size: 14px;
  
}.dropProf>div:nth-child(1) button{
  float: left;
  padding: 10px 20px ;
  color: #ff4293;
  border: none;
  border: 1px solid rgba(86, 84, 84, 0.251);
  background: transparent;
  margin: 5px;
  
}
.dropProf .dropflex{
  display: flex;
  flex-direction: column;
  
  align-items: start;
 
}
.dropProf .dropflex a{
  text-decoration: none;
  color: rgba(0, 0, 0, 0.788);
  font-family: sans-serif;
  font-size: 12px;
}
.dropProf .dropflex a:hover{
 color: black; 
 font-weight: bold;
}


.home-icons > div:nth-child(1):hover {
  border-bottom: 4px solid #ff4293;
  padding: 18px 10px;
}
.home-icons > div:nth-child(1):hover .dropProf {
  visibility: visible;
}

/* ============SIGNUP============ */
.signup{
  background-color: red;
}




@media screen and (max-width: 1200px) {
  nav ul {
    width: 50%;
    margin-right: 0px;
  }
  nav img {
    width: 60px;
    margin: 0 20px;
  }
  #search {
    width: 200px;
  }
}
@media screen and (max-width: 1000px) {
  nav img {
    width: 60px;
    /* margin-right: 200px; */
  }
  .search {
    width: 58%;
  }
  #menu {
    display: block;
  }
  nav ul {
    display: none;
  }
  #search {
    width: 100%;
  }
}
