* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}
#signup {
  width: 100%;
  height: 100vh;
  background: linear-gradient(white, rgb(249, 213, 220));
}

.login {
  width: 33%;
  display: block;
  background-color: white;
  margin: 50px auto;
  height: 70vh;
  
}
.login img {
  width: 100%;
}

.login .form {
  border: 1px solid rgba(0, 0, 0, 0.368);
  width: 80%;
  margin: 20px auto;
  padding: 0 20px;
  color: rgba(0, 0, 0, 0.626);
}

.login h3 {
  color: rgba(0, 0, 0, 0.785);
margin:20px 40px;
}
span {
  font-size: 14px;
}
p {
  font-size: 11px;
  color: rgba(0, 0, 0, 0.785);
  text-align: center;
}
p span {
  color: #ff4293;
  font-weight: bold;
  cursor: pointer;
  font-size: 11px;
}
.login input {
  border: none;
  font-size: 1rem;
  padding: 10px 20px;
}
.login input:focus {
  outline: none;
}
.login input[type="submit"] {
  width: 80%;
  display: block;
  font-size: 1rem;
  background-color: #ff4293;
  margin: 20px auto;
  color: white;
  cursor: pointer;
}
.login input[type="submit"]:hover {
  background-color: #ea0e6d;
}
